.notes-board-container {
  display: -ms-flexbox;
  display: flex;
}

.content {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  max-width: 50%;
  -ms-flex-direction: column;
      flex-direction: column;
  position: relative;
}

.emoji {
  width: 90px;
  height: auto;
  position: absolute;
  left: 10px;
  top: 10px;
}

.content:first-of-type {
  border-right: 2px solid grey;
}

.content-background {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  height: 100%;
  width: 100%;
  background-image: url('https://i.imgur.com/Xro5AuL.png');
}

.content-background::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 250px;
  top: 0;
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(255,255,255,0)),to(rgba(255,255,255,1)));
  background: -webkit-linear-gradient(bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
  background: -o-linear-gradient(bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
  background: linear-gradient(to top, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
}

.content-background::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 250px;
  bottom: 0;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0)),to(rgba(255,255,255,1)));
  background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
  background: -o-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
  background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
}

.input-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: center;
      justify-content: center;
  min-height: 120px;
  padding: 20px 0 50px;
}

.notes-container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-column-gap: 60px;
  grid-row-gap: 20px;
  padding: 20px 60px;
  overflow: auto;
}

.notes-container div {
  position: relative;
}

@media screen and (min-width: 768px) {
  .notes-container {
    grid-template-columns: repeat(2, 1fr);
  }
}


